.product {
  &__photo {
    @include hover(transform, scale(1.1));

    width: 100%;
  }

  &__price {
    font-weight: 700;
    line-height: 140%;
  }

  &__tittle {
    font-weight: 700;
    font-size: 16px;
  }

  &__description {
    letter-spacing: 0.15px;
    font-weight: 400;
  }
}
