.icon {
  display: block;
  height: 24px;
  width: 24px;
  background-repeat: no-repeat;
  transition: transform $transition-time;

  @include hover(transform, scale(1.2));

  @include on-tablet {
    height: 32px;
    width: 32px;
  }

  &--phone {
    background-image: url(/src/images/icon-phone-call-2x.png);
    background-size: contain;
    position: relative;
    display: flex;
    align-items: center;

    @include on-desktop {
      &:hover::after {
        content: '+1 234 5555-55-55';
        position: absolute;
        line-height: 16px;
        font-size: 12px;
        font-weight: 700;
        left: -147px;
        letter-spacing: 2px;
        text-decoration: none;
        color: $main-text-color;
      }
    }
  }

  &--menu {
    background-image: url(/src/images/icon-burger-menu-2x.png);
    background-size: contain;

    &:hover {
      background-image: url(/src/images/icon-burger-menu-hover-2x.png);
      background-size: contain;
    }
  }

  &--close {
    background-image: url(/src/images/icon-close-2x.png);
    background-size: contain;
  }
}
